/* Badges */

.badge {
  border-radius: 4px;
  font-size: 12px;
  line-height: 1;
  padding: 6px 6px;
  font-weight: normal;
  color: $white;
  &.badge-pill {
    border-radius: 10rem;
  }
}

/*Badge outlined variations*/
@each $color, $value in $theme-colors {
  .badge-outline-#{$color} {
    @include badge-outline-variations($value);
  }
}