.profile-sidebar{
  display: block;
  position: fixed;
  top:0;
  left: -$profile-settings-width;
  bottom: 0;
  width: $profile-settings-width;
  height: 100vh;
  min-height: 100%;
  background: $card-bg;
  @include transition-duration($action-transition-duration);
  @include transition-timing-function($action-transition-timing-function);
  @include transition-property(left, box-shadow);
  z-index: 9999;
  overflow-y: auto;
  &.open {
    left:0;
    -webkit-box-shadow: 7px 0px 80px -9px rgba(0,0,0,0.15);
    -moz-box-shadow: 7px 0px 80px -9px rgba(0,0,0,0.15);
    box-shadow: 7px 0px 80px -9px rgba(0,0,0,0.15);
  }
  .profile-settings-close {
    position: absolute;
    top: 8px;
    right: 1rem;
    color: $white;
    background: transparent;
    @include border-radius(4px);
    padding: 0 3px;
    cursor: pointer;
    @include transition-duration(0.2s);
    z-index: 999;
    &:hover{
      background: rgba($white,0.3);
    }
  }
  .profile-sidebar-header {
    background: theme-color(primary);
    color: $white;
    padding: 2rem 1rem;
    .profile-sidebar-thumb {
      img {
        width: 90px;
        height: 90px;
        border-radius: 100%;
      }
    }
    .profile-sidebar-info {
      color: $white;
      margin-top: .5rem;
      .name {
        font-weight: bold;
        margin-bottom: .25rem;
        font-size: 20px;
      }
      .designation {
        font-size: 12px;
        font-weight: 600;
      }
    }
    .profile-social-icons {
      width: 80%;
      margin:0 auto;
      margin-top: 1rem;
      padding: .625rem;
      background: $white;
      border-radius:.25rem;
      a {
        color: theme-color(primary);
        padding: 0 .625rem;
        i {
          font-size: 24px;
        }
      }
    }
  }
  .profile-sidebar-body {
    padding: .875rem 0;
    .list-profile-items {
      list-style: none;
      padding-left: 0;
      .profile-item {
        padding: .625rem 1rem;
        border-bottom: 1px solid $border-color;
        .profile-sidebar-link {
          display: flex;
          justify-content: space-between;
          align-content: center;
          i {
            font-size: 1.5rem;
          }
          .item-title {
            color: $body-color;
            margin-bottom: 0;
            font-weight: 600;
            margin-left: .625rem;
            margin-top: .25rem;
          }
          .item-detail {
            color: $text-muted;
            margin-left: .625rem;
            font-size:14px;
            margin-bottom: 0;
          }
          &:hover {
            text-decoration: none;
          }
        }
        &:hover {
          background: $profile-settings-hover-bg;
        }
      }
    }
  }
}