/* Demo Styles */
// Add spacing to Boostrap components for demo purpose
.template-demo {
  > .btn {
    @extend .mt-3;
    @extend .mr-2;
  }

  .slider-wrap {
    height: 100px;
  }

  > .btn-toolbar {
    @extend .mt-3;
    @extend .mr-3;
  }

  > .btn-group,
  >  .btn-group-vertical {
    @extend .mt-2;
    @extend .mr-2;
  }

  .progress {
    margin-top: 1.5rem;
  }

  .circle-progress {
    @extend .mt-2;
  }

  > h2,
   > h3,
   > h4,
   > h5,
   > h6,
  > h1 {
    border-top: 1px solid $border-color;
    padding: 0.5rem 0 0;
  }

  .ul-slider {
    &.noUi-horizontal {
      margin-top: 2rem;
    }

    &.noUi-vertical {
      margin-right: 2rem;
    }
  }

  > .dropdown {
    display: inline-block;
    @extend .mr-2;
    margin-bottom: 0.5rem;
  }

  nav {
    .breadcrumb {
      margin-bottom: 1.375rem;
    }

    &:last-child {
      .breadcrumb {
        margin-bottom: 0;
      }
    }
  }

  .editable-form {
    > .form-group {
      border-bottom: 1px solid $border-color;
      padding-bottom: 0.8rem;
      margin-bottom: 0.8rem;
    }
  }

  .circle-progress {
    padding: 15px;
  }

  .circle-progress-block {
    @extend .mb-3;
    @extend .px-2;
  }
  
}

.demo-modal {
  position: static;
  display: block;

  .modal-dialog {
    @extend .mt-0;

    &.modal-lg {
      max-width: 100%;
    }
  }
}

.loader-demo-box {
  @extend .border;
  @extend .border-secondary;
  @extend .rounded;
  width: 100%;
  height: 200px;
  @extend .d-flex;
  @extend .align-items-center;
}

.dropdown-menu-static-demo {
  height: 250px;
  margin-bottom: 20px;
  .dropdown-menu {
    z-index: 0;
  }
}

.rounded-legend {
  ul {
    li {
      list-style-type: none;
      color: $text-muted;
      font-size: .75rem;
      .legend-dots {
        width: 1rem;
        height: 1rem;
        border-radius: 100%;
        display: inline-block;
        vertical-align: text-bottom;
        margin-right: .5rem;
        .rtl & {
          margin-left: .5rem;
        }
      }
    }
  }
  &.legend-horizontal {
    @include display-flex;
    ul {
      li {
        display: inline-block;
        margin-right: 1.5rem;
        .rtl & {
          margin-right: auto;
          margin-left: 1.5rem;
        }
      }
    }
  }
  &.legend-top-right {
    ul {
      float: right;
      .rtl & {
        float: left;
      }
    }
  }
  &.legend-vertical {
    ul {
      li {
        margin-top: 1rem;
      }
    }
  }
}

.demo-chart {
  height: 300px;
  @media (max-width: 767px) {
    height: 200px;
  }
}

.chartjs-legend {
  ul {
    margin-bottom: 0;
    list-style: none;
    padding-left: 0;
    @include display-flex;
    @include align-items(center);
    @include flex-direction(row);
    @include flex-wrap(wrap);
    @include justify-content(start);
    li {
      margin-right: 8%;
      @include display-flex;
      @include align-items(center);
      span {
        height: 1rem;
        width: 1rem;
        margin-right: .4rem;
        display: inline-block;
        font-size: $default-font-size;
        border-radius: 100%;
      }
    }
  }
  .rtl & {
    ul {
      padding-right: 0;
      li {
        margin-right: 0;
        margin-left: 8%;
        span {
          margin-right: 0;
          margin-left: 1rem;
        }
      }
    }
  }
  &.analytics-legend {
    ul {
      padding-left: 0;
    }
  }
}

.documentation {
  padding-top: 0;
  max-width: 100%;
  .content-wrapper {
    padding-top: 0;
    min-height: calc(100vh - #{$footer-height});
  }
}
.doc-table-contents,
.doc-header {
  position: fixed;
}
.doc-header {
  padding-top: 24px;
  padding-bottom: 24px;
  z-index: 2;
  background: $content-bg;
  z-index: 999;
}

.doc-content {
  padding-top: 157px;
}

.doc-table-contents {
  overflow: auto;
  max-height: calc(100% - 157px);
  ul {
    li {
      margin-top: 10px;
    }
  }
}

.doc-credits {
  ul {
    li {
      margin-bottom: 10px;
      a {
        margin-left: .25rem;
      }
    }
  }
}

.demo-vector-map {
  width: 100%;
  height: 500px;
}

.sparkline-demo-chart {
  canvas{
    max-width: 100%;
  }
}

.layouts-preview-main-wrapper {
  background: #efefef;
  .layouts-preview-wrapper {
    .preview-item {
      -webkit-transition-duration: 0.45s;
      -moz-transition-duration: 0.45s;
      -o-transition-duration: 0.45s;
      transition-duration: 0.45s;
      transition-property: color;
      -webkit-transition-property: transform;
      transition: transform .2s;
      a {
        position: relative;
        text-decoration: none;
        img {
          border-radius: 4px;
          -webkit-box-shadow: 0 25px 50px rgba(8,21,66,.06);
                  box-shadow: 0 25px 50px rgba(8,21,66,.06);
          -webkit-transition: all .3s ease-in-out;
          transition: all .3s ease-in-out;
        }
        .item-title {
          @extend .d-flex;
          @extend .align-items-center;
          @extend .justify-content-center;
          border-radius: 0 0 4px 4px;
          h3 {
            font-size: 1.25rem;
            color: theme-color(dark);
            margin-bottom: 1rem;
          }
        }
        .img-wrapper {
          text-align: center;
          border: solid 10px #fff;
          border-radius: 10px;
          -webkit-box-shadow: 0px 0px 26px 5px rgba(129,122,161,0.07);
          box-shadow: 0px 0px 26px 5px rgba(129,122,161,0.07);
      }
      }
      &:hover {
        a {
          img {
            -webkit-box-shadow: 0 5px 12px rgba(126,142,177,.2);
            box-shadow: 0 5px 12px rgba(126,142,177,.2);
          }
        }
      }
      &:hover {
        transform: scale(1.02);
      }
    }
  }  
  .advantages {
    -webkit-box-shadow: 0 0 60px 10px rgba(220,220,220,0.3);
    -o-box-shadow: 0 0 60px 10px rgba(220,220,220,0.3);
    box-shadow: 0 0 60px 10px rgba(220,220,220,0.3);
    .advanteges-item {
      min-height: 274px;
      @media (max-width: 1024px) {
        min-height: 354px;
      }
      @media (max-width: 768px) {
        min-height: auto;
      }
    }
    .advantages-border-left {
      border-left: 1px solid #fff;
      @media (max-width: 991px) {
        border-left:none;
      }
    }
    .advantages-border-bottom {
      border-bottom: 1px solid #fff;

    }
    .advantages-border-top {
      border-top: none;
      @media (max-width: 991px) {
        border-top: 1px solid #fff;
      }
    }
  }
}
.mobile-only-100 {
  @media (max-width: 991px) {
    width: 100%;
  }
}